import React from 'react'
import $ from 'jquery';


const EduStyle = {
    color:'#3F51B5'
};

const CollStyle = {
    color:'#004D40'
};

const TitleStyle = {
    textAlign:'center',
    color:'#3F51B5'
};

class Projects extends React.Component {
    render() {
        return (
            <div>
                <br></br>
                <br></br>
            <h4 style={TitleStyle}>Projects</h4>
            <br></br>
            <ul>
                <li><strong style={CollStyle}>Co-founder and CTO, FuryDevs (Startup) </strong><i>Jan,2019 - Present</i></li>
                <p>Co-founded a Web Development startup along with other 3 members. Holds responsibility as Chief Technology Officer (CTO).</p>

                <li><strong style={CollStyle}>Content Writing Intern, BrandFoxie (Work from Home)</strong><i>Feb, 2019 - March, 2019</i></li>
                <p>Wrote articles about the Technical concepts like Block Chain, Amazon Products, etc.</p>
            </ul>
            <br/><br/><br/><br/><br/>
            </div>
        )
    }
}

export default Projects