import React from 'react';
import ReactDOM from 'react-dom';
import $ from 'jquery';
import { Route, Link, NavLink, BrowserRouter as Router, Switch, HashRouter } from 'react-router-dom'
import './index.css';
import App from './App';
import Home from './home';
import Education from './education';
import Experience from './experience';
import Skills from './skills';
import Projects from './projects';
import Courses from './courses';
import Achievements from './achievements';
import * as serviceWorker from './serviceWorker';

ReactDOM.render(<Home />, document.getElementById('root'));

$(document).ready(function(){
    $(".Link").click(function(){
        $(".ImageDiv").hide(2000);
        $(".HomeBarOne").addClass("HomeBar");

        $(".HomeDisplayStyle").addClass("HomeDisplayStyleTwo");
        $(".HomeDisplayStyleTwo").removeClass("HomeDisplayStyle");
    });
});


const Navigation = (
    <HashRouter basename="/nav/">
        <div className="NavBarArea">
            <ul className="NavStyle">
            
                <li>
                    <NavLink aciveClassName="active" to="/education" className="Link">Education</NavLink>
                </li>
                <li>
                    <NavLink aciveClassName="active" to="/experience" className="Link">Experience</NavLink>
                </li>
                <li>
                    <NavLink aciveClassName="active" to="/skills" className="Link">Skills</NavLink>
                </li>
                <li>
                    <NavLink aciveClassName="active" to="/projects" className="Link">Projects</NavLink>
                </li>
                <li>
                    <NavLink aciveClassName="active" to="/courses" className="Link">Courses</NavLink>
                </li>
                <li>
                    <NavLink aciveClassName="active" to="/achievements" className="Link">Achievements</NavLink>
                </li>
            </ul>
            <Switch>
                <Route path="/education" component={Education}/>
                <Route path="/experience" component={Experience}/>
                <Route path="/skills" component={Skills}/>
                <Route path="/projects" component={Projects}/>
                <Route path="/courses" component={Courses}/>
                <Route path="/achievements" component={Achievements}/>
            </Switch>
        </div>
    </HashRouter>
)

ReactDOM.render(Navigation, document.getElementById('navigation'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
