import React from 'react'
import $ from 'jquery';


const EduStyle = {
    color:'#3F51B5'
};

const CollStyle = {
    color:'#004D40'
};

const sty = {
    textAlign:'center'
};

const TitleStyle = {
    textAlign:'center',
    color:'#3F51B5'
};


class Skills extends React.Component {
    render() {
        return (
            <div>
                <br></br>
                <br></br>
                <h4 style={TitleStyle}>Technical Skills</h4>
                <div className="anisep"></div>
                <br></br>
                <div className="row">
                    
                    <div className="col-sm" style={sty}>
                        <h2 style={CollStyle}>Languages</h2><br></br>
                        <p>Python</p>
                        <p>C</p>
                        <p>C++</p>
                        <p>Java</p>
                        <p>JavaScript</p>
                        <p>PHP</p>
                        <p>mySQL</p>
                    </div>

                    <div className="col-sm" style={sty}>
                        <h2 style={CollStyle}>Development</h2><br></br>
                        <p>HTML</p>
                        <p>CSS</p>
                        <p>ReactJS</p>
                        <p>Bootstrap</p>
                        <p>Android Development (using Java)</p>
                    </div>

                    <div className="col-sm" style={sty}>
                        <h2 style={CollStyle}>Tools</h2><br></br>
                        <p>Adobe After Effects</p>
                        <p>Adobe Photoshop</p>
                        <p>Sony Vegas</p>
                        <br/>
                        <h3 style={CollStyle}>Interested</h3>
                        <p>Web Technologies</p>
                        <p>Databases</p>
                        <p>Algorithms</p>                   
                    </div>
                </div>


                <br/><br/><br/><br/><br/>
                
            </div>
        )
    }
}

export default Skills