import React from 'react'
import $ from 'jquery';
import CRR from './CRR.jpeg';
import Seminar from './Seminar.jpg';


const EduStyle = {
    color:'#3F51B5'
};

const CollStyle = {
    color:'#004D40'
};

const TitleStyle = {
    textAlign:'center',
    color:'#3F51B5'
};

const ImageStyle = {
    width:'200px',
    height:'150px'
};

const ImageCaption = {
    textAlign:'center',
    margin:'40px'
};

const ImageCaptionTwo = {
    textAlign:'center',
    margin:'40px',
    listStylePosition:'inside'
};

class Achievements extends React.Component {
    render() {
        return (
            <div>
                <br></br>
                <br></br>
            <h4 style={TitleStyle}>Achievements and Accomplishments</h4>
            <br></br>
            <div className="text-center">
                <img src={CRR} className="rounded" style={ImageStyle}/><br/>
                <p style={ImageCaption}>Won first prize in Web Development Contest conducted at Technical Fest of Sir. C.R.R. College, Eluru. Designed a complete static college web site in 30 minutes.</p>
            </div>  

            <ul style={ImageCaptionTwo}>
                <li>Won the 4 hour hackathon conducted by CSE Department. Developed Automatic Certificate Generator using JavaScript</li>
            </ul>
            <div className="text-center">
                <img src={Seminar} className="rounded" style={ImageStyle}/><br/>
                <p style={ImageCaption}>Conducted seminar on '<i>Technical Talk on C Programming</i>' for 2nd year CSE students. Guided some students for Mini Projects.</p>
            </div>

            <ul style={ImageCaptionTwo}>
                <li>Served as the Joint Secretary for the College CSE Department Association ”Technozola” in the 2nd year and conducted few coding events.</li>
            </ul>

            <ul style={ImageCaptionTwo}>
                <li>Member of the Editorial desk of College Magazine '<i>UR Times</i> '.</li>
            </ul>

            <ul style={ImageCaptionTwo}>
                <li>Selected as one of the Android Developer for the College Android Application.</li>
            </ul>

            <ul style={ImageCaptionTwo}>
                <li>Attended one day Online Conference on ”Amazon Web Services” powered by AWS and Intel.</li>
            </ul>

            <ul style={ImageCaptionTwo}>
                <li>Participated in Technical Fests of JNTU Narasaraopet and JNTU Kakinada.</li>
            </ul>

            <ul style={ImageCaptionTwo}>
                <li>Participated in Inter NIT Coding Contest(Online).</li>
            </ul>
            <br/><br/><br/><br/><br/>
            </div>
        )
    }
}

export default Achievements