import React from 'react'
import SuryaImage from './SuryaImage.jpg';
import './index.css';
import $ from 'jquery';

const ImageStyle = {
    width:'200px',
    height:'200px',
    marginTop:'200px'
};

const TitleStyle = {
    textAlign:'center',
    color:'#b71c1c'
};

const DisplayStyle = {
    textAlign:'center'
};


class Home extends React.Component {
    render() {
        return (
            <div className="HomeBarOne">
                <div className="ImageDiv">
                    <img src={SuryaImage} class="rounded-circle mx-auto d-block ImageStyle" alt="Surya Image"></img>
                 </div>
                 <div className="HomeDisplayStyle animated slideInUp">
                     <br/>
                     <h1 className="display-4 HomeTitleStyle">Surya Kandikonda</h1>
                     <h4 className="caption">Design | Code | Develop</h4>
                     <h6 className="BasicContact">suryasaikandikonda@gmail.com | +91 73063 74787 | Eluru, India.</h6>
                     <p>Download resume from <a href="https://drive.google.com/file/d/1P0V59Xv1Dv7STE3ylNo7aQAtmCw46RMp/view?usp=sharing" target="_blank">here</a></p><hr/>
                 </div>
            </div>
        )
    }
}

export default Home